@import url("https://fonts.googleapis.com/css2?family=Frank+Ruhl+Libre:wght@300..900&display=swap");

.main-container {
  max-width: 90%;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  gap: 50px;
}

.main-container .profile {
  width: 25%;
  margin-top: auto;
  margin-bottom: 20vh;
  padding-left: 30px;
}
.main-container .profile .name {
  line-height: 1.75em;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  margin-bottom: 2vh;
  font-size: 15px;
}
.main-container .profile .jobTitle {
  line-height: 1.9em;
  margin-bottom: 20px;
  font-size: 13px;
}
.main-container .profile .work {
  line-height: 1.9em;
  font-size: 13px;
}
.main-container .profile .work a {
  color: black;
  text-decoration: none;
  border-bottom: 2px solid black;
  transition: border-color 0.25s ease;
}
.main-container .profile .work a:hover {
  text-decoration: line-through;
  border-bottom-color: transparent;
}

.main-container .link-container {
  width: 65%;
  display: flex;
  flex-direction: column;
  margin-top: auto;
  margin-bottom: 15vh;
}
.main-container .link-container a {
  font-size: 7vw;
  font-family: "Frank Ruhl Libre", serif;
  font-optical-sizing: auto;
  font-style: normal;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 300;
  color: black;
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out,
    font-size 0.3s ease-in-out;
  box-sizing: border-box;
}
.main-container .link-container a:hover {
  font-weight: 300;
  font-style: italic;
  transform: translate(5px);
}
@media (min-width: 768px) and (max-width: 900px) {
  .main-container {
    max-width: 100%;
    padding: 10px 30px;
    flex-direction: column;
    gap: 10px;
  }
  .main-container .profile {
    width: 100%;
    margin: 0;
    margin-top: 20vh;
  }
  .main-container .link-container {
    width: 100%;
    margin: 0;
    margin-top: 20px;
  }
  .main-container .link-container a {
    font-size: 11vw;
  }
}
@media (max-width: 768px) {
  .main-container {
    max-width: 100%;
    min-height: 0;
    padding: 10px 30px;
    flex-direction: column;
    gap: 25px;
  }
  .main-container .profile {
    width: 100%;
    margin: 0;
    margin-top: 10vh;
    padding-left: 0;
  }
  .main-container .link-container {
    width: 100%;
  }
  .main-container .link-container a {
    font-size: 11vw;
  }
}
